/**
 * 
 * 
 */
const env = (typeof import.meta !== 'undefined' ? import.meta.env : importMetaEnv);
export class Env {
    /**
     * 
     */
    static getString(key) {
        const value = env[key];
        return typeof value === 'string' ? value : '';
    }
    /**
     * 
     */
    static getNumber(key) {
        const value = env[key];
        return value ? Number(value) : 0;
    }
    /**
     * 
     */
    static getBoolean(key) {
        const value = env[key];
        return value === 'true';
    }
    /**
     * 
     */
    static get appName() {
        return this.getString('VITE_APP_NAME');
    }
    /**
     * 
     */
    static get appVersion() {
        return this.getString('VITE_APP_VERSION');
    }
    /**
     * 
     */
    static get appDescription() {
        return this.getString('VITE_APP_DESCRIPTION');
    }
    /**
     *  API  URL
     */
    static get apiBaseUrl() {
        return this.getString('VITE_API_BASE_URL');
    }
    /**
     *  API 
     */
    static get apiTimeout() {
        return this.getNumber('VITE_API_TIMEOUT');
    }
    /**
     * 
     */
    static get isDebug() {
        return this.getBoolean('VITE_ENABLE_DEBUG');
    }
    /**
     * 
     */
    static get isMockData() {
        return this.getBoolean('VITE_ENABLE_MOCK_DATA');
    }
    /**
     * 
     */
    static get isAnalytics() {
        return this.getBoolean('VITE_ENABLE_ANALYTICS');
    }
    /**
     *  Google Analytics ID
     */
    static get googleAnalyticsId() {
        return this.getString('VITE_GOOGLE_ANALYTICS_ID');
    }
    /**
     *  Sentry DSN
     */
    static get sentryDsn() {
        return this.getString('VITE_SENTRY_DSN');
    }
    /**
     * 
     */
    static get publicPath() {
        return this.getString('VITE_PUBLIC_PATH');
    }
    /**
     * 
     */
    static get isDevelopment() {
        return env.NODE_ENV === 'development';
    }
    /**
     * 
     */
    static get isProduction() {
        return env.NODE_ENV === 'production';
    }
    /**
     * 
     */
    static get environment() {
        return env.NODE_ENV || 'unknown';
    }
}
